.block {
  padding: 40px 0;
}

.block-80-80 {
  padding: 40px 0;
}

.block-lg {
  padding: 60px 0 60px;
}

.block-inner {
  margin: 80px 0;
}

.block-inner:first-child {
  margin-top: 0;
}

.block-inner:last-child {
  margin-bottom: 0;
}

.block-inner__links .btn {
  margin: 30px 0;
}

.block-inner-80-105 {
  margin: 40px 0;
}

.block-sm {
  padding: 25px 0 25px;
}

.block__content > div:first-child > * {
  margin-top: 0;
}

.block__content > div:last-child > * {
  margin-bottom: 0;
}

.main-content .block__content > *:first-child {
  margin-top: 0;
}

.block > *.container > *:first-child {
  margin-top: 0;
}

.b__hd {
  margin-bottom: 42px;
}

.block__title {
  margin: 0;
}

.block__title h2 {
  margin-top: 0;
  margin-bottom: 42px;
}

.block__link {
  display: none;
}

.block__link a {
  margin: auto 0 auto auto;
}

.b__ft {
  margin-top: 30px;
}

.block .block__title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media only screen and (min-width: 768px) {
  .block {
    padding: 40px 0 60px;
  }

  .block-80-80 {
    padding: 60px 0;
  }

  .block-lg {
    padding: 60px 0 60px;
  }

  .block-inner-80-105 {
    margin: 40px 0 60px;
  }

  .block-sm {
    padding: 30px 0 30px;
  }
}

@media only screen and (min-width: 1024px) {
  .block {
    padding: 80px 0 105px;
  }

  .block-80-80 {
    padding: 80px 0;
  }

  .block-lg {
    padding: 105px 0 105px;
  }

  .block-inner-80-105 {
    margin: 80px 0 105px;
  }

  .block-sm {
    padding: 50px 0 50px;
  }

  .block__title {
    display: flex;
    flex: 1 auto;
  }

  .block__link {
    display: flex;
    flex: 0 auto;
  }
}

@media only screen and (max-width: 767px) {
  .block__title h2 {
    margin-bottom: 30px;
  }
}